html {
  background-color: rgb(71, 71, 111);
}
.App {
  text-align: center;
  color: rgb(196, 190, 190);
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 30px;
}

.App-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 24px;
  color: rgb(237, 231, 231);
}

.project-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 10px;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  height: 300px;
  background-color: #2d2d30;
  border-radius: 24px;
  gap: 10px;
}

.description {
  text-align: left;
  width: 90%;
  height: 170px;
  margin: 0;
}

.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20%;
}

a {
  padding: 4px;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.5rem;
  width: 3.5rem;
  border-radius: 8px;
  text-decoration: none;
  background-color: #007acc;
  color: rgb(203, 197, 197);
}

h1 {
  margin: 0;
  margin-top: 10px;
  color: rgb(201, 192, 124);
}

@media screen and (min-width: 1000px) {
  .App {
    justify-content: center;
    height: 100dvh;
    padding: 0;
  }
}
